@charset "UTF-8";
/* =Reset default browser CSS.
Based on work by Eric Meyer: http://meyerweb.com/eric/tools/css/reset/index.html
-------------------------------------------------------------- */
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, font, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td {
  border: 0;
  font-family: inherit;
  font-size: 100%;
  font-style: inherit;
  font-weight: inherit;
  margin: 0;
  outline: 0;
  padding: 0;
  vertical-align: baseline; }

:focus {
  outline: 0; }

ol, ul {
  list-style: none; }

table {
  border-collapse: separate;
  border-spacing: 0; }

caption, th, td {
  font-weight: normal;
  text-align: left; }

blockquote:before, blockquote:after, q:before, q:after {
  content: ""; }

blockquote, q {
  quotes: "" ""; }

a img {
  border: 0; }

figure {
  margin: 0; }

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

/* -------------------------------------------------------------- */
body {
  color: #1f1f1f;
  font: 12px verdana,"ヒラギノ丸ゴ ProN W4","Hiragino Maru Gothic ProN","メイリオ","Meiryo","ＭＳ Ｐゴシック","MS PGothic",Sans-Serif;
  line-height: 1.5;
  border-left: 8px solid #134091;
  border-right: 8px solid #134091;
  -webkit-text-size-adjust: none;
  background: #fff; }

html {
  border-left: 5px solid #2871d7;
  border-right: 5px solid #2871d7; }

/* リンク設定
------------------------------------------------------------*/
a {
  color: #fff;
  text-decoration: none; }

a:hover {
  color: #ccc; }

a:active, a:focus {
  outline: 0; }

/* 全体
------------------------------------------------------------*/
#wrapper {
  margin: 20px auto;
  padding: 0 1%;
  width: 100%;
  position: relative; }

.inner {
  margin: 0 auto;
  width: 100%; }

/*************
/* ヘッダー
*************/
h1 {
  font-size: 12px;
  line-height: 1.6;
  font-weight: normal; }

#header {
  overflow: hidden;
  padding: 5px 0 0; }

* html #header {
  height: 1%; }

/*************
/* ロゴ
*************/
#header .logo {
  display: flex;
  padding: 0; }

.logo a {
  font-size: 20px;
  font-weight: bold;
  line-height: 1;
  font-family: "Palatino Linotype", "Book Antiqua", Palatino,"ヒラギノ明朝 Pro W3","Hiragino Mincho Pro","ＭＳ Ｐ明朝","MS PMincho",serif; }

.logo span {
  font-size: 12px;
  font-weight: normal; }

.info {
  float: right;
  padding: 20px 0; }

.tel strong {
  font-size: 20px;
  font-weight: bold; }

/**************************
/* トップナビゲーション
**************************/
ul#topnav {
  display: flex;
  flex-direction: nowrap;
  justify-content: center;
  clear: both;
  overflow: hidden;
  margin: 0 auto 30px;
  background: #134091;
  border-radius: 5px; }

* html ul#topnav {
  height: 1%; }

ul#topnav li {
  text-align: center; }

ul#topnav a {
  width: 156px;
  font-size: 12px;
  display: block;
  padding: 15px 0;
  color: #fff;
  border-left: 1px solid #2871d7; }

ul#topnav li:first-child a {
  border: 0; }

ul#topnav li:last-child a {
  width: 155px; }

ul#topnav li.active a, ul#topnav a:hover {
  background: #2871d7; }

/**************************
/* メイン画像（トップページ+サブページ）
**************************/
#mainBanner {
  clear: both;
  line-height: 0; }

#mainBanner .inner {
  position: relative; }

#mainBanner img {
  max-width: 100%;
  height: 400px;
  object-fit: cover; }

.slogan {
  position: absolute;
  max-width: 100%;
  height: auto;
  bottom: 10px;
  left: 10px;
  padding: 300px 10px;
  line-height: 1.4;
  z-index: 100;
  background: url(images/tp2.png); }

.subImg .slogan {
  left: auto;
  right: 10px; }

.slogan h2 {
  padding-bottom: 5px;
  font-size: 25px; }

.slogan h3 {
  font-size: 15px; }

/**************************
/* グリッド
**************************/
.gridWrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  clear: both;
  padding: 20px 0;
  overflow: hidden; }

* html .gridWrapper {
  height: 1%; }

.colDark, .colLight {
  padding-bottom: 10px;
  color: #fff;
  background: #2871d7; }

.colLight {
  background: #134091; }

.colDark h3, .colLight h3 {
  margin: 10px 10px 5px;
  font-size: 14px;
  border-bottom: 1px solid #fff; }

.colDark a, .colLight a {
  display: block;
  color: #ccc; }

.colDark a:hover, .colLight a:hover {
  color: #fff; }

.grid p {
  padding: 5px 10px; }

.grid ul {
  margin: 5px 10px; }

.grid li {
  padding: 5px 0;
  list-style: inside;
  border-top: 1px solid #000;
  border-bottom: 1px solid #333; }

.grid li:first-child {
  border-top: 0; }

.grid li:last-child {
  border-bottom: 0; }

.grid li a {
  color: #000; }

.col3 li a {
  color: #fff; }

.gridWrapper img {
  max-width: 100%;
  height: auto; }

/* フッター内のグリッド(3カラム) */
#footer .grid {
  color: #fff;
  background: transparent; }

#footer .col3 ul {
  margin: 0 0 10px 5px; }

#footer .col3 li {
  padding: 0 7px 0 5px;
  margin-bottom: 3px;
  list-style: none;
  border-bottom: 0;
  display: inline-block;
  border: 0;
  border-right: 1px solid #fff; }

@media (max-width: 768.98px) {
  .gridWrapper {
    display: block; } }

/*************
メイン コンテンツ
*************/
section.content {
  margin-bottom: 20px;
  font-size: 14px;
  overflow: hidden; }

* html section.content {
  height: 1%; }

section.content p {
  margin-bottom: 5px; }

h3.heading, #sub .grid h3 {
  padding: 5px 10px;
  margin-bottom: 10px;
  font-size: 16px;
  color: #fff;
  background: #134091;
  border-top: 2px solid #2871d7;
  border-radius: 4px; }

#sub .grid h3 {
  margin-right: 10px; }

.alignleft {
  float: left;
  clear: left;
  margin: 3px 10px 10px 1px; }

.alignright {
  float: right;
  clear: right;
  margin: 3px 1px 10px 10px; }

img.frame, #gallery img {
  border: 4px solid #333; }

#gallery .gridWrapper {
  padding: 0; }

#gallery img {
  margin: 5px; }

section#gallery {
  overflow: visible; }

/*************
テーブル
*************/
table.table {
  border-collapse: collapse;
  width: 100%;
  margin: 10px auto; }

table.table th, table.table td {
  padding: 5px;
  border: 1px solid #2871d7; }

table.table tr:first-child th, table.table tr:first-child td {
  border-top: #2871d7 4px solid; }

table.table th {
  font-weight: bold;
  letter-spacing: 1px;
  white-space: nowrap;
  color: #fff;
  background: #134091; }

/*************
/* フッター
*************/
#footer .inner {
  clear: both;
  padding: 20px 0;
  overflow: hidden;
  background: #134091;
  border-radius: 5px 5px 0 0; }

* html #footer {
  height: 1%; }

#footer .copyright {
  font-size: 11px; }

/* PC用 */
@media only screen and (min-width: 1680px) {
  #wrapper, .inner, ul#topnav {
    width: 1680px;
    padding: 0; }
  #wrapper {
    padding-bottom: 20px; }
  #main {
    float: right;
    width: 690px; }
  #subpage #sub {
    float: left;
    width: 235px;
    overflow: hidden; }
  /* グリッド */
  .grid {
    width: 235px; }
  .col2 {
    width: 470px; }
  .col3 {
    width: 705px; }
  #gallery .grid {
    width: 230px; }
  #subpage #sub .grid {
    width: 100%;
    float: none;
    padding-bottom: 30px; }
  .colDark:first-child {
    border-radius: 6px 0 0 6px; }
  .colLight:last-child {
    border-radius: 0 6px 6px 0; } }

/* モニター幅960px以下 */
@media only screen and (max-width: 960px) {
  h1 p.inner {
    padding: 0 2%;
    width: 96%; }
  #header .logo {
    padding-left: 10px; }
  #header .info {
    padding-right: 10px; }
  ul#topnav {
    clear: both;
    width: 100%;
    border-radius: 0; }
  ul#topnav li {
    width: 33%;
    border-right: 1px solid #2871d7;
    border-bottom: 1px solid #2871d7;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box; }
  ul#topnav li:nth-child(3n) {
    border-right: 0;
    width: 34%; }
  ul#topnav li a {
    width: 100%;
    border: 0; }
  ul#topnav li:first-child a, ul#topnav li:last-child a {
    width: 100%; }
  .grid {
    width: 50%;
    margin: 2px 0; }
  .col2 {
    float: none;
    width: 100%;
    margin: 0 0 20px; }
  .grid img {
    float: left;
    margin-right: 5px; }
  #gallery .grid {
    width: 31%;
    margin: 0 1%;
    padding: 0; }
  #gallery .grid img {
    float: none; }
  section.content {
    padding: 0 10px; }
  section.content img {
    max-width: 90%;
    height: auto; }
  #sub .grid {
    padding-bottom: 20px; }
  #footer {
    padding: 0; }
  #footer .grid {
    float: none;
    width: 100%;
    margin-bottom: 5px;
    text-align: center; } }

/* スマートフォン 横(ランドスケープ) */
@media only screen and (max-width: 640px) {
  .grid {
    width: 100%; }
  .grid img {
    float: none;
    margin: 20px auto 10px; }
  .grid p {
    text-align: left; }
  img.frame, #gallery img {
    border-width: 2px; }
  .alignleft, .alignright {
    float: none;
    display: block;
    margin: 0 auto 10px; }
  #footer .grid p {
    text-align: center; } }

/* スマートフォン 縦(ポートレート) */
@media only screen and (max-width: 480px) {
  #header .logo {
    padding-top: 10px; }
  .info {
    padding: 10px 0; }
  ul#topnav li {
    width: 50%; }
  ul#topnav li:nth-child(3n) {
    border-right: 1px solid #2871d7;
    width: 50%; }
  ul#topnav li:nth-child(2n), ul#topnav li:nth-child(6n) {
    border-right: 0; }
  #mainBanner h2, #mainBanner h3 {
    font-size: 80%; } }
